import styled, { keyframes } from 'styled-components';

const loading = keyframes`
  from {
    background-position: 0 0;
    /* rotate: 0; */
  }

  to {
    background-position: 100% 100%;
    /* rotate: 360deg; */
  }
`;

const Form = styled.form`
	box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.05);
	background: rgba(0, 0, 0, 0.02);
	border: 5px solid white;
	padding: 20px;
	font-size: 1.5rem;
	line-height: 1.5;
	font-weight: 600;
	label {
		display: block;
		margin-bottom: 1rem;
		margin-right: 1rem;
		padding-right: 50px;
	}
	input,: 0.5rem;
	textarea,
	select {
		width: 100%;
		padding: 1rem;
		border: 1px solid black;
		&:focus {
			outline: 0;
			border-color: ${(props) => props.theme.lightRed};
		}
	}
	button,
	input[type='submit'] {
		width: auto;
		background: ${(props) => props.theme.lightRed};
		color: white;
		border: 0;
		font-size: 2rem;
		font-weight: 600;
		padding: 0.5rem 1.2rem;
	}
	fieldset {
		border: 0;
		padding: 0;

		&[disabled] {
			opacity: 0.5;
		}
		&::before {
			height: 10px;
			margin-bottom: 10px;
			content: '';
			display: block;
			background-image: linear-gradient(
				to right,
				${(props) => props.theme.cream} 0%,
				${(props) => props.theme.lightblue} 50%,
				#ff3019 100%
			);
		}
		&[aria-busy='true']::before {
			background-size: 50% auto;
			animation: ${loading} 0.5s linear infinite;
		}
	}
`;

Form.displayName = 'Form';

export default Form;
